import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'

     import users from '@/views/modules/users/list'
    import dictionary from '@/views/modules/dictionary/list'
    import kuaidiyuan from '@/views/modules/kuaidiyuan/list'
    import kuaijian from '@/views/modules/kuaijian/list'
    import liuyan from '@/views/modules/liuyan/list'
    import news from '@/views/modules/news/list'
    import yonghu from '@/views/modules/yonghu/list'
    import dictionaryJijianxinxi from '@/views/modules/dictionaryJijianxinxi/list'
    import dictionaryKuaijian from '@/views/modules/dictionaryKuaijian/list'
    import dictionaryNews from '@/views/modules/dictionaryNews/list'
    import dictionarySex from '@/views/modules/dictionarySex/list'





//2.   
const routes = [{
    path: '/index',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    } ,{
        path: '/users',
        name: '',
        component: users
      }
    ,{
        path: '/dictionaryJijianxinxi',
        name: '',
        component: dictionaryJijianxinxi
    }
    ,{
        path: '/dictionaryKuaijian',
        name: '',
        component: dictionaryKuaijian
    }
    ,{
        path: '/dictionaryNews',
        name: '',
        component: dictionaryNews
    }
    ,{
        path: '/dictionarySex',
        name: '',
        component: dictionarySex
    }


    ,{
        path: '/dictionary',
        name: '',
        component: dictionary
      }
    ,{
        path: '/kuaidiyuan',
        name: '',
        component: kuaidiyuan
      }
    ,{
        path: '/kuaijian',
        name: '',
        component: kuaijian
      }
    ,{
        path: '/liuyan',
        name: '',
        component: liuyan
      }
    ,{
        path: '/news',
        name: '',
        component: news
      }
    ,{
        path: '/yonghu',
        name: '',
        component: yonghu
      }


    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '',
    redirect: '/index'
  }, /**/
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})

export default router;
